import { useWeb3Modal } from '@web3modal/wagmi/react';
import { useAccount } from 'wagmi';

export default function ConnectButton() {

const { address, isConnecting, isDisconnected, isConnected } = useAccount();

  const { open } = useWeb3Modal()
  if (isConnecting) 
  return (
    <>
      <button onClick={() => open()} className="btn connect-btn mt-2 mt-lg-3">Connecting</button>
    </>
  )
  else if (isDisconnected) 
  return (
    <>
      <button onClick={() => open()} className="btn connect-btn mt-2 mt-lg-3">Connect</button>
    </>
  )
  else if (isConnected) 
  return (
    <>
      <button onClick={() => open()} className="btn connect-btn mt-2 mt-lg-3">{address.slice(0, 4) + "..." + address.slice(-2)}</button>
    </>
  )
  
}